@import url(https://fonts.googleapis.com/css?family=Open+Sans);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


html, body {
    height: 100%;
    margin: 0;
}

input:focus,
select:focus,
textarea:focus,
button:focus {
    outline: none;
}

.row {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 65vw;
    height: 75vh;
    left: 17.5%;
    top: 14%;
    position: absolute;
    border-radius: 50px;
    box-shadow: 6px 8px 9px 6px rgba(0, 0, 0, 0.25);
}

.row-reg {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 65vw;
    height: 75vh;
    left: 16.5%;
    top: 14%;
    position: absolute;
    border-radius: 50px;
    box-shadow: 6px 8px 9px 6px rgba(0, 0, 0, 0.25);
}


.col {
    display: flex;
    flex-direction: column;
    flex-basis: 100%;
}

.mainBackground {
    background: url(/george/static/media/Background_Main.874b2113.svg) no-repeat center;
    background-size: cover;
    position: relative;
    top: 0px;
    height: 100vh;
}

#info-col {
    background-color: #fbfbfb;
    flex: 2 1;
    align-items: left;
    text-align: left;
    border-radius: 30px 0px 0px 30px;
}

#info-col > p {
    margin-left: 10%;
    margin-top: 4%;
    font-family: 'Open Sans',serif;
}

#info-col-intro {
    background-color: #fefefe;
    flex: 3 1;
    align-items: left;
    text-align: left;
    border-radius: 30px 0px 0px 30px;
}

#signup-col {
    background-color: #fefefe;
    flex: 3 1;
    align-items: left;
    padding-top :2vh;   
    padding-left: 2vw;
    padding-right: 2vw;

    display: flex;
    flex-direction: column;
    border-radius: 0px 30px 30px 0px;
}

#signup-col-intro {
   background-color: #fbfbfb;
   flex: 2.5 1;
   align-items: left;
   padding-top :2vh;   
   padding-left: 2vw;
   padding-right: 2vw;

   display: flex;
   flex-direction: column;
   border-radius: 0px 30px 30px 0px;
}

.sign-up-content {
    flex: 1 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: relative;
    left: 4.5%;
    top: 7.5%;
    margin-bottom: 10vh;
}

.sign-up-grid-content {
    width: 92.5%;
    position: relative;
    top: 7.5%;
    left: 4.5%;
    display: grid;
    grid-column-gap: 20px;
    grid-row-gap: 0px;
}

.label1 { 
    grid-area: 1 / 1 / 2 / 2; 
}

.label2 { 
    grid-area: 1 / 2 / 2 / 3; 
}

.label3 { 
    grid-area: 2 / 1 / 3 / 2;
 }

.label4 {
     grid-area: 2 / 2 / 3 / 3;
}

.label5 {
    grid-area: 3 / 1 / 3  / 3;
}
     

.textInput {
    padding-left: 5%;
    font-family: 'Open Sans',serif;
    color: #2B3A70;
    font-weight: 800;
}

.textInputGrid {
    padding-left: 10%;
    font-family: 'Open Sans',serif;
    color: #2B3A70;
    font-weight: 800;
    width:95% !important;
}


.title-text {
    color: black;
    font-size: 30px;
    font-weight: 600;
    position: relative;
    top: 2%;
    font-family: 'Open Sans',serif;
}

.info-text {
    color: black;
    font-size: medium;
    font-family: 'Open Sans',serif;
}

.description-text {
    font-size: 15px;
    font-family: 'Open Sans',serif;
}

#icon {
    width: 75%;
    margin: 5vh;
