}

#log-in-button { 
    margin-top: 5vh;
    margin-bottom: 5vh;
    padding-top: 2vh;
    padding-bottom: 2vh;
    width: 90%;
    color: #ffffff;
    background: #4A4E69;
    box-shadow: 4px 4px 6px 1px rgba(0, 0, 0, 0.25);
    border-radius: 25px;
    border: 10px #FFFFFF;
    font-family: 'Open Sans',serif;
    font-size: 17.5px;
    cursor: pointer;
}

#register-button {
    margin-top: 5vh;
    margin-bottom: 5vh;
    padding-top: 2vh;
    padding-bottom: 2vh;
    width: 98%;
    color: #ffffff;
    background: #4A4E69;
    box-shadow: 4px 4px 6px 1px rgba(0, 0, 0, 0.25);
    border-radius: 25px;
    border: 10px #FFFFFF;
    font-family: 'Open Sans',serif;
    font-size: 17.5px;
    cursor: pointer;
}

#redirect-button {
    position: relative;
    top: 18.5%;
    left: 25%;
    margin-top: 3vh;
    margin-bottom: 0.5vh;
    padding-top: 2vh;
    padding-bottom: 2vh;
    width: 50%;
    color: #ffffff;
    background: #4A4E69;
    box-shadow: 4px 4px 6px 1px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    border: 10px #FFFFFF;
    font-family: 'Open Sans',serif;
    font-size: 17.5px;
    cursor: pointer;
    text-decoration: none;
    text-align: center;
}

#redirect-button:hover {
    background: #383A47;
}

#log-in-button:hover {
    background: #383A47;
}

#register-button:hover {
    background: #383A47;
}

#sign-up-text {
    text-align: right;
    font-family: 'Open Sans',serif;
    position: relative;
    top: 2.5%;
