/* ----------Search bar ------------ */

.searchBar {
    width: 100%;
    height: 90px;
    background: #FFFFFF;
    box-shadow: 4px 4px 4px 4px rgba(201, 201, 201, 0.3);
    display: flex;
    align-items: center;
    justify-content: center;
}

.icon {
    width: 60px;
    height: 60px;

    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    left: 5%;
}

.appIcon {
    width: 70px;
    height: 70px;
}

.searchField {
    width: 70%;
    height: 55px;
    font-size: 32px;

    background: #FBFBFB;
    box-shadow: 4px 4px 8px 4px rgba(0, 0, 0, 0.1);
    border-radius: 50px;
    border: 0;
    padding-left: 40px;

    margin-right: 1%;
    margin-left: 5%;
    margin-bottom: 0;
    
}

.searchField:focus {
    outline: none;
    box-shadow: 0 0 5px rgba(81, 203, 238, 1);
    border: 1px solid rgba(81, 203, 238, 1);
}

.searchIcon {
    position: relative;
    right: 10%;
}

.logoutContainer, .checkoutContainer {
    position: absolute;
    width: 60px;
    height: 60px; 

    border-radius: 55px;
    background: #FBFBFB;
    box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.2);

    display: flex;
    align-items: center;
    justify-content: center;
}

.logoutContainer {
    right: 5%;
}

.checkoutContainer {
    right: 120px;
}

.logoutIcon, .marketIcon {
    width: 35px;
    height: 35px; 
}

.logoutContainer:hover, .searchIcon:hover, .marketIcon {
    cursor: pointer;
}

