.itemContainer {
    width: 300px;
    height: 100px;
    
    background: #FFFFFF;
    box-shadow: 4px 4px 4px 4px rgba(0, 0, 0, 0.04);
    border-radius: 15px;
    display: flex;
    align-items: center;
    /* justify-content: space-around; */
    margin: 2%;
}

.imageReceieved {
    width: 104px;
    height: 20px;
    /* top: 50%; */
    /* position: absolute; */
}

#checkoutTitle { 
    font-family: Open Sans;
    font-size: 40px;
    font-weight: bold;
    margin-bottom: 5vh;
}

#pcExpressIcon {
    height: 32px;
    width: 96px;
    margin-top: -.3em;
}

#loblawsIcon {
    height: 30px;
    width: 140px;
    margin-top: -.3em;
}

.itemText {
    font-family: Open Sans;
    font-style: normal;
    font-weight: 600;
    font-size: 25px;
    line-height: 34px;
    color: #282828;
    margin-left: 10%;
}

.nameText {
    font-family: Open Sans;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    color: #282828;
}

.priceText {
    font-family: Open Sans;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    color: #282828;
    text-align: right;
}

.totalPriceText {
    font-family: Open Sans;
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    color: #282828;
    text-align: right;
}

.centerView {
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-top: 55px;
}

.itemLabel {
    width: 40%
}

.listViews {
    display: flex;
    width: 80%;
    justify-content: space-between;
}

.listViewsButtons {
    display: flex;
    flex-direction: column;
    margin-bottom: 55px;
}

.listContainer {
    position: relative;
    background-color: white;
    width: 450px;
    border-radius: 1em;
    margin: 1em;
    padding: 2em;
    padding-top: 0em;
    box-shadow: 3px 3px 3px 3px rgba(0, 0, 0, 0.06);
    /* shado */
}

.labelContainer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}


.logoWrapper {
    background-color: white;
    padding-top: 1em;
    margin-left: -2em;
    margin-right: -2em;
    margin-bottom: 30px;
    border-radius: 1em 1em 0em 0em;
    height: 50px;
    display: flex;
    justify-content: center;
}

.totalPriceContainer {
    margin-top: 2em;
    margin-bottom: -1.5em;
}

.actionButton { 
    margin-top: 3vh;
    padding-top: 3vh;
    padding-bottom: 3vh;
    /* width: 90%; */
    color: #ffffff;
    background: #4A4E69;
    box-shadow: 4px 4px 6px 1px rgba(0, 0, 0, 0.25);
    border-radius: 2em;
    border: 10px #FFFFFF;
    font-family: 'Open Sans',serif;
    font-size: 17.5px;
    cursor: pointer;
    width: 80vw;
}

.actionButton:hover {
    background: #383A47;
}

#blueWrapper {
    background-color: #0071CE;
}

#blackWrapper {
    background-color: #000000;
}

.discountTag {
    width: 150px;
    height: 50px;
    background: #E85454;
    border-radius: 50px;
    color: #FFFFFF;
    display: flex;
    align-items: center;
    justify-content: center;

    position: relative;
    bottom: 30px;
}

.discountTagText {

    font-family: Open Sans;
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 34px;
    /* identical to box height */

}

.paymentButton {
    margin-top: 30px;
}